import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import {message } from 'antd';
import Scrollbars from 'react-custom-scrollbars';

import classnames from 'classnames';
import MorePopup from './more-popup';							//更多弹窗
import CashPopup from './cash-popup';							//现金弹窗
import CategoryDiscount from './category-discount-popup';		//分类折弹窗
import MemberDiscount from './member-discount-popup/';		    //会员折弹窗
import MemberInfo from './member-payment-popup/';		    	//会员信息弹窗
import RefundPopup from './card-tuikuan-popup/';		   		//退款弹窗
import AccreditPopup from 'components/accredit-popup';		    //授权弹窗
import PromptPopup from 'components/prompt-popup';		   		//提示弹窗
import InvoicePopup from './invoice-popup';		   				//开发票弹窗
import Wechat from './wechat-pay-popup/';		   				//微信支付弹窗
import DiscountCoupon from './discount-coupon-popup';			//折扣券
import DiscountPrice from './discount-price-popup/';			//折扣弹窗
import AdjustProductDiscout from './adjust-product-discount/'; //调整商品折扣

import './modal.css';
import './settlement.css';

message.config({
	top: 300,
	duration: 1
});

/**************** 支付方式组件 *****************/
function PayMent({payMent, clickHandle}) {
	return <li onClick={clickHandle}> 
		<i className={classnames({
			'iconfont': true,
			'icon-account_btn_vip': payMent.paymentName === '会员卡',
			'icon-account_btn_cash': payMent.paymentName === '现金',
			'icon-account_btn_guazhang': payMent.paymentName === '银联',
			'icon-account_btn_wechat': payMent.paymentName === '微信',
			'icon-account_btn_alipay': payMent.paymentName === '支付宝',
			'icon-account_btn_integral': payMent.paymentName === '积分折现',
			'icon-account_btn_k': payMent.paymentName === 'K币',
			'icon-account_btn_voucher': payMent.paymentName === '代金券'
		})}></i>
		<span className={classnames({
			"online":true,
			"hide-online":payMent.paymentName === '现金' || payMent.paymentName === '积分折现'||payMent.paymentName === 'K币' || payMent.paymentName === '挂账' ||payMent.paymentName === '银联',
		})}>线上</span>
		<p>{payMent.paymentName}</p>
	</li>
}
/**************** 支付方式组件 *****************/


/**************** 已下单组件 *****************/
function DidOrder({ index, dishesStore, dishes, modifyDidOrder, markOrderHandle, cancelMarkOrderHandle }) {
	let produceStatus = dishes.combo && (!dishes.childs || !dishes.childs.length) ? dishes.comboProduceStatus : dishes.produceStatus;
	return <li className={classnames({
		'select': dishes.selected
	})}  onClick={modifyDidOrder}>
		<div className="row-one">
			<span className="serial">{index>-1 && index+1}</span>
			<span className="name">
				{dishes.tagIDs&&dishes.tagIDs.split(',').map(id => {
					let tag;
					switch(id) {
						case '20': //免做
							tag = <span key={id} className="mianzuo">免</span>;
							break;
						case '21': //加急
							tag = <span key={id} className="xian">先</span>;
							break;
						case '22': //催菜
							tag = <span key={id} className="cui">催</span>;
							break;
						case '23': //打包
							tag = <span key={id} className="dabao">打包</span>;
							break;
						default:
					}
					return tag;
				})}
				{dishes.childs && dishes.childs.length || !dishes.comboName ? dishes.productName+(dishes.optionName ? '('+dishes.optionName+')' : '') : dishes.comboName}
			</span>
			<span className="number">
				{(() => {
					if(dishes.needWeigh) {
						return dishes.floatQuantity;
					}else if(dishes.servingQuantity) {
						return dishes.servingQuantity+'/'+dishes.quantity;
					}else {
						return dishes.quantity
					}
				})()}
			</span>
			<span className="price">￥
			{(() => {
				if(dishes.comboDetaPrice) {
					return dishes.comboDetaPrice;
				}else if(dishes.totalAmount){
					return dishes.totalAmount;
				}
			})()}
			</span>
		</div>
		<div className="row-two">
			{!!dishes.valueNames && <div className="taste">
				<span className="matter">{dishes.valueNames}</span>
			</div>}
			{!!dishes.memo && <span className="remark">备注：{dishes.memo}</span>}
			<div className="zeng-block">
				{(() => {
					let tag;
					switch(dishes.aLaCarteMethod) {
						case 685: //退菜
							tag = <span className="tui">退</span>;
							break;
						case 686: //赠菜
							tag = <span className="zeng">赠</span>;
							break;
						case 687: //加菜
							tag = <span className="jia">加</span>;
							break;
						default:
					}
					return tag;
				})()}
				{false && <span>[老板朋友]</span>}
			</div>
		</div>
		<div className="row-three">
			<div className="huadan-block">
				{(() => {
					let statusTag;
					switch(produceStatus) {
						case 694:
							statusTag = <span className="dengjiao">等叫</span>;
							break;
						case 830:
							statusTag = <span className="weichengzhong">未称重</span>;
							break;
						case 693:
							statusTag = <span className="zhizuo">制作中</span>;
							break;
						case 981:
							statusTag = <span className="chupin">已出品</span>;
							break;
						case 982:
							statusTag = <span className="shangcai">已上菜</span>;
							break;
						case 698:
							statusTag = <span className="chaoshi">超时</span>;
							break;
						default: 
					}
					return statusTag;
				})()}
				{produceStatus !== 982 && //已上菜
				produceStatus !== 830 && //未称重
				produceStatus !== 694 && //等叫
				produceStatus !== 685 && //退菜
				<span className="huadan" onClick={(event) => {
					markOrderHandle();
					event.stopPropagation();
				}}>划单</span>}
				{produceStatus === 982 && <span className="huadan" onClick={(event) => {
					cancelMarkOrderHandle();
					event.stopPropagation();
				}}>取消划单</span>}
			</div>
		</div>
	</li>
}
/**************** 已下单组件 *****************/
@inject('settlementStore') @observer
class Settlement extends Component {

	constructor(props,context){
		super(props,context);
		props.settlementStore.getAmountSituation({subOrderID:this.props.params.subOrderID});
		props.settlementStore.getPaymentMethod({subOrderID:this.props.params.subOrderID});
		props.settlementStore.getTableUseInfo({subOrderID:this.props.params.subOrderID});
		props.settlementStore.getOrderInfo({subOrderID:this.props.params.subOrderID});
		props.settlementStore.getDidOrderList(this.props.params.subOrderID);
		props.settlementStore.getDisCountTypeName(this.props.params.subOrderID);
		props.settlementStore.getUpdateDiscountInfo(this.props.params.subOrderID);
	
		
	}
	state={
		discountType:'',
		showDiscount:false,
		popup: "", //现金弹窗
		refundpopup: "", 
		memberpopup:"", //会员折
		promptpopup:"", 
		discountPricePopup:"", 
		accreditpopup:"", 
		stopSettlement:"暂结",
		waitAmount:'', 
		wechat:'', 
		relevanceTable:false, 
		current:-1,
		alertShow:'',//折扣提示显示
		memberPayment:'',//会员卡支付
		memberInfo:'',//会员信息
	}
	componentDidMount() {
		let settlementStore=this.props.settlementStore;
		// this.props.settlementStore.getAmountSituation();
		settlementStore.receivableDate=[];
		settlementStore.cashLish=[];
		settlementStore.amountReceived=0;
	}

	componentDidUpdate() {
		let  settlementStore= this.props.settlementStore;
		let feedback = settlementStore.feedback;
		if(feedback) {
			//提示
			switch(feedback.status) {
				case 'success':
					message.success(feedback.msg,settlementStore.closeFeedback());
					break;
				case 'warn':
					message.warn(feedback.msg,settlementStore.closeFeedback());
					break;
				default:
					message.info(feedback.msg,settlementStore.closeFeedback());
			}
		}
  }

//计算金额----减
	minusCountAmount=(ele)=>{
		let settlementStore = this.props.settlementStore;
		settlementStore.amountReceived=parseFloat(settlementStore.amountReceived)-parseFloat(ele.paymentAmount);//已收
		settlementStore.amountReceived=settlementStore.amountReceived.toFixed(2);
		settlementStore.waitCollectAmount=parseFloat(settlementStore.amountSituation.payableAmount)-parseFloat(settlementStore.amountReceived);//待收金额
		settlementStore.waitCollectAmount=settlementStore.waitCollectAmount.toFixed(2);
		if(settlementStore.waitCollectAmount<0){
			settlementStore.waitCollectAmount=0;
		}
		settlementStore.oddChange=parseFloat(settlementStore.amountReceived)-parseFloat(settlementStore.amountSituation.payableAmount);//找零
		settlementStore.oddChange=settlementStore.oddChange.toFixed(2);
		if(settlementStore.oddChange<=0){
			settlementStore.oddChange=0;
		}
		if(settlementStore.waitCollectAmount==0){//是否可结账
			settlementStore.settlementBtnDisable=true;
		}else{
			settlementStore.settlementBtnDisable=false;
		}
		settlementStore.invoiceAmount=settlementStore.amountReceived-settlementStore.oddChange;//可开票金额
	}

//计算金额----加
	addCountAmount=(ele)=>{
		let settlementStore = this.props.settlementStore;
		settlementStore.amountReceived=parseFloat(settlementStore.amountReceived)+parseFloat(ele.paymentAmount);//已收
		settlementStore.amountReceived=settlementStore.amountReceived.toFixed(2);
		settlementStore.waitCollectAmount=parseFloat(settlementStore.amountSituation.payableAmount)-parseFloat(settlementStore.amountReceived);//待收金额
		settlementStore.waitCollectAmount=settlementStore.waitCollectAmount.toFixed(2);
		if(settlementStore.waitCollectAmount<0){
			settlementStore.waitCollectAmount=0;
		}
		settlementStore.oddChange=parseFloat(settlementStore.amountReceived)-parseFloat(settlementStore.amountSituation.payableAmount);//找零
		settlementStore.oddChange=settlementStore.oddChange.toFixed(2);
		if(settlementStore.oddChange<=0){
			settlementStore.oddChange=0;
		}
		if(settlementStore.waitCollectAmount==0){//是否可结账
			settlementStore.settlementBtnDisable=true;
		}else{
			settlementStore.settlementBtnDisable=false;
		}
		settlementStore.invoiceAmount=settlementStore.amountReceived-settlementStore.oddChange;//可开票金额
	}
	
	//增加付款
	addPayMent=(payment)=>{
		let settlementStore = this.props.settlementStore;
		// 会员卡支付
		if(payment.paymentMethodID===8){
			this.setState({memberPayment:<MemberDiscount title={payment.paymentName} inputValue={settlementStore.waitCollectAmount} onOk={(value)=>{
					this.setState({memberPayment:''});
					this.setState({memberInfo:<MemberInfo onOk={()=>{

					}} onCancel={()=>{
						this.setState({memberInfo:''});
					}}/>});
					let item = {
						paymentMethodID: payment.paymentMethodID,
						title: payment.paymentName, 
						paymentAmount: value,
					};
					//settlementStore.getMemberCard({item:item,authCode:value, subOrderID: this.props.params.subOrderID,paidAmount:settlementStore.paymentMethodList, paymentMethodID: payment.paymentMethodID, paymentAmount:parseFloat(settlementStore.amountSituation.waitCollectAmount)});
					
			}} onCancel={()=>{
					this.setState({memberPayment:''})
				}}
			/>})
		}
		// 微信线上支付判断
		if(payment.paymentMethodID===1 || payment.paymentMethodID===2){
			this.setState({wechat:<Wechat title={payment.paymentName} inputValue={settlementStore.waitCollectAmount} onOk={(value)=>{
					this.setState({wechat:''});
					let cashStatus;
					if(payment.paymentName==="现金"){
						cashStatus=2;
					}else{
						cashStatus=1;
					}
					let item = {
						paymentMethodID: payment.paymentMethodID,
						title: payment.paymentName, 
						paymentAmount: settlementStore.waitCollectAmount,
						isCash: cashStatus
					};
					settlementStore.addReceivableDate(item);
					settlementStore.getOnlinePayment({item:item,authCode:value, subOrderID: this.props.params.subOrderID,paidAmount:settlementStore.paymentMethodList, paymentMethodID: payment.paymentMethodID, paymentAmount:parseFloat(settlementStore.amountSituation.waitCollectAmount)});
					
			}} onCancel={()=>{
					this.setState({wechat:''})
				}}
			/>})
		}
		if(payment.paymentMethodID===5){
			this.setState({popup:<CashPopup title={payment.paymentName} onOk={(value)=>{
			if(payment.paymentName==="微信App"){
				this.setState({wechat:<Wechat title={payment.paymentName} inputValue={value} onOk={()=>{
					this.setState({wechat:''});
					settlementStore.getOrderPayment({subOrderID: this.props.params.subOrderID,paidAmount:settlementStore.paymentMethodList, paymentMethodID: payment.paymentMethodID, paymentAmount:parseFloat(value),isOnline:payment.isOnline});
				}} onCancel={()=>{
					this.setState({wechat:''})
				}}
				/>})
			}else{
				let cashStatus;
				if(payment.paymentName==="现金"){
					cashStatus=2;
				}else{
					cashStatus=1;
				}
				let item = {
					paymentMethodID: payment.paymentMethodID,
					title: payment.paymentName, 
					paymentAmount: value,
					isCash: cashStatus,
					isOnline:payment.isOnline
				};
				settlementStore.addReceivableDate(item);
				this.addCountAmount(item);//收款--已收、待收、找零、可开票金额、是否可结账
				// settlementStore.getOrderPayment({item:item,subOrderID: this.props.params.subOrderID,paidAmount:settlementStore.paymentMethodList, paymentMethodID: payment.paymentMethodID, paymentAmount:parseFloat(value),isOnline:payment.isOnline});
				
				this.setState({popup:""})
			}
			}} onCancel={()=>{
				this.setState({popup:""})
			}}/>})
		}

	}

	

	 
	render() {
		let settlementStore= this.props.settlementStore;
		let feedback = settlementStore.feedback;
		let orderInfo = settlementStore.orderInfo;
		let morePopup;
		let adjustProduct;
		let discountPopup;
		let categoryDiscountPopup;
		// let memberDiscountPopup;
		let accreditpopup;
		let invoicepopup;
		let operatePrompt;
		//关联桌无有数据时
		let relevanceTable;
		if(orderInfo.comTableCode===null){
			relevanceTable=<div className="relate-desk"></div>
		}else{
			relevanceTable=<div className="relate-desk">关联桌台：<span>{orderInfo.comTableCode}</span></div>
		}
		//折扣券弹窗显示
		if(this.props.settlementStore.discountCouponShow){
          discountPopup=<DiscountCoupon />
       }
		
		//整单备注
		let remark;
		
		if(orderInfo.memo===null){
			remark=<div className="info-remarks">整单备注：无</div>
		}else{
			remark=<div className="info-remarks">整单备注：{orderInfo.memo}</div>
			
		}
		if(feedback&& feedback.status === 'error') {
			//错误提示
			feedback.okClick = () =>{
				if(feedback.confirmClick) feedback.confirmClick();
				feedback.cancelClick();
			}
			feedback.cancelClick = () =>{
				settlementStore.closeFeedback();
			}
			// operatePrompt = <Prompt message={feedback} />
		}

		if(settlementStore.moreShow){
			morePopup = <MorePopup />
		}
		if(settlementStore.categoryDiscount){
			categoryDiscountPopup=<CategoryDiscount />
		}
		if(this.props.settlementStore.adjustProductShow){
			adjustProduct = <AdjustProductDiscout />
		}
	
		if(settlementStore.invoicePopup){
			invoicepopup=<InvoicePopup subOrderID={this.props.params.subOrderID} />
		}
	

		//授权组件
		if(settlementStore.accreditPopupShow){
			accreditpopup=<AccreditPopup title={"授权"} />
		}



	// 结账主页面右边金额下的按钮
		let btnBlock;
		btnBlock = settlementStore.mbtns.map((ele,index)=>{
            return  <li key={index} onClick={()=>{
					// ele.selected = !ele.selected;
					if(settlementStore.disable){
						message.info("已打过折，不能再进行折扣");
					}else{
							this.setState({current:index});
					if(ele.text==="更多"){
						settlementStore.getMore();
					}
					if(ele.text==="分类折"){
						settlementStore.getCategoryDiscount();
					}
					if(ele.text==="折扣券"){
						settlementStore.getDiscountCoupon();
					}
					if(ele.text==="减免"){
						settlementStore.getEmployeeCanReduceAmount({subOrderID:this.props.params.subOrderID});
					}
					if(ele.text==="会员折"){
						this.setState({memberpopup:<MemberDiscount title={"会员折"} subOrderID={this.props.params.subOrderID} onOk={(value,type)=>{
							alert(111)
							settlementStore.getMemberDiscount({subOrderID: this.props.params.subOrderID, memberCodeType:type, memberCode:value})
							this.setState({memberpopup:""})
						}} onCancel={()=>{
							this.setState({memberpopup:""})
						}}
						/>})
					}
					if(ele.text==="收服务费"){
						settlementStore.getServiceCharge({subOrderID:this.props.params.subOrderID});
					}
					
					if(ele.text==="权限折"||ele.text==="全单折"||ele.text==="单品折"||ele.text==="减免"||ele.text==="免单"||ele.text==="免服务费"){
						this.setState({accreditpopup:<AccreditPopup  title={ele.text} onOk={()=>{
						this.setState({accreditpopup:""});
						this.setState({discountPricePopup:<DiscountPrice alert="" title={ele.text} onOk={(value)=>{
							if(ele.text==="权限折"){
								settlementStore.getAuthorityDiscount({subOrderID:this.props.params.subOrderID,discountPercentage:parseFloat(value)})
								this.setState({alertShow:''})
							}
							if(ele.text==="全单折"){
								settlementStore.getOrderDiscount({subOrderID:this.props.params.subOrderID,discountPercentage:parseFloat(value)})
								this.setState({alertShow:''})
							}
							this.setState({discountType:ele.text+value+"折"});//显示折扣方式
							this.setState({discountPricePopup:""});
							// this.setState({showDiscount:true});//显示取消折扣按钮
						}} onCancel={()=>{
							this.setState({discountPricePopup:""})
						}}/>});
						if(ele.text==="减免"){
							if(settlementStore.employeeCanReduceList.code===0){
								this.setState({discountPricePopup:<DiscountPrice alert={"你本次可减免金额最多为"+settlementStore.bigDecimal} title={ele.text} onOk={(value)=>{
									settlementStore.getReduceAmount({subOrderID:this.props.params.subOrderID,discountAmount:value})
								this.setState({discountPricePopup:""})
								}} onCancel={()=>{
									this.setState({discountPricePopup:""})
								}}/>});
							}else{
								this.setState({discountPricePopup:""})
							}
						}
						if(ele.text==="免服务费"){
							settlementStore.getFreeServiceCharge({subOrderID:this.props.params.subOrderID});
							this.setState({discountPricePopup:""});
						}
						if(ele.text==="免单"){
							settlementStore.getOrderFree({subOrderID:this.props.params.subOrderID});
							this.setState({discountPricePopup:""});
						}
						}} onCancel={()=>{
							this.setState({accreditpopup:""})
						}}
					/>})
						
					}
					
				
					
					}
				}} 
					className={classnames({
					"active":index===this.state.current,
					"disable":settlementStore.disable,
				})}
				>{ele.text}
			</li>
        });

//主页面已收款添加数据  
// isCash=0   什么都不显示
// isCash=1   显示退字
// isCash=2   显示关闭 --现金
	let receivableBlock;
	if(settlementStore.receivableDate.length>0){
	let recordsIndex = 1;
	receivableBlock = 	settlementStore.receivableDate.map((ele,index)=>{
		let deletedBlock;
		if(ele.isCash === 2){
			deletedBlock=<i className="iconfont icon-shanchu1" onClick={()=>{
				this.setState({promptpopup:<PromptPopup title="提示" textValue={ele.paymentAmount} onOk={()=>{
					settlementStore.getRefund({subOrderID:this.props.params.subOrderID, paidAmount:settlementStore.paymentMethodList, paymentMethodID: ele.paymentMethodID, paymentAmount:parseFloat(ele.paymentAmount),oddChange:parseFloat(settlementStore.oddChange)});				
					settlementStore.receivableDate.splice(index,1);
					this.setState({promptpopup:""});
					this.minusCountAmount(ele);//退款--已收、待收、找零、可开票金额、是否可结账
					}} onCancel={()=>{
						this.setState({promptpopup:""});
					}}
				>
					<div className="prompt">
						<p className="warning">确定删除现金收款</p>
						<p className="delele-text">删除金额<span>{ele.paymentAmount}</span>元</p>
					</div>
				</PromptPopup>})
			}}>
			</i>
		}else if(ele.isCash === 1){
			deletedBlock=<i onClick={()=>{
				this.setState({refundpopup:<RefundPopup title={ele.title}  textValue={ele.paymentAmount}  onOk={(value)=>{
					settlementStore.getRefund({paymentMethodID: ele.paymentMethodID,paidAmount:settlementStore.paymentMethodList , paymentAmount:parseFloat(ele.paymentAmount),oddChange:parseFloat(settlementStore.oddChange)})
					settlementStore.Refunds(ele, index);
					this.setState({refundpopup: ""});
					}} onCancel={()=>{
						this.setState({refundpopup: ""})
					}} />});
			}}>退</i>
		}
	//如果是线上支付，要二次确认
		let confirmPayment;
			confirmPayment=<div className={classnames({
				'onlineBtn':true,
				'hideBtn':!settlementStore.confirmPaymentStatus
			})}>
				<button onClick={()=>{
					this.setState({promptpopup:<PromptPopup title="提示" textValue={ele.paymentAmount} onOk={()=>{
						//settlementStore.getRefund({subOrderID:this.props.params.subOrderID, paidAmount:settlementStore.paymentMethodList, paymentMethodID: ele.paymentMethodID, paymentAmount:parseFloat(ele.paymentAmount),oddChange:parseFloat(settlementStore.oddChange)});				
						// settlementStore.receivableDate.splice(index,1);
						this.setState({promptpopup:""});
						this.addCountAmount(ele);//退款--已收、待收、找零、可开票金额、是否可结账
						settlementStore.confirmPaymentStatus=false;

						}} onCancel={()=>{
							this.setState({promptpopup:""});
						}}
					>
						<div className="prompt">
							<p className="delele-text">确定收款成功？<span>{ele.paymentAmount}</span>元</p>
						</div>
					</PromptPopup>})
				}}>确定</button>
				<button onClick={()=>{
					settlementStore.receivableDate.splice(index,1);
					settlementStore.confirmPaymentStatus=false;
				}}>取消</button>
			</div>
		return <li key={index} className={ele.isCash==2 ?'cash':'normal'}>
					{ele.paymentAmount>0?<span>{recordsIndex++}</span>:<span>&nbsp;</span>}
					<span>{ele.title}</span>
					<span>￥{ele.paymentAmount}</span>
					{deletedBlock}
					{confirmPayment}
			</li>
		});
	}


	// 应收金额
	let payableAmount;
	if(settlementStore.amountSituation.payableAmount<=0){
		payableAmount=<div className="receive">应收：<span>0</span></div>
	}else{
		payableAmount=<div className="receive">应收：<span>{settlementStore.amountSituation.payableAmount }</span></div>
	}
	// 服务费
	let freeServiceStyle;
	if(settlementStore.amountSituation.serviceFee<=0){
		freeServiceStyle=<li><span className="title">服 务 费：</span><span className={settlementStore.freeServiceStyle}>0</span></li>
	}else{
		freeServiceStyle=<li><span className="title">服 务 费：</span><span className={settlementStore.freeServiceStyle}>{settlementStore.amountSituation.serviceFee}</span></li>
	}
	


		
		return (
			 <div id="order_account">
				<div className="account-header">
					结账
					<i className="iconfont icon-order_btn_back"onClick={() => {
						//返回桌台界面
						this.context.router.goBack();
					}}></i>
				</div>
				<div id="account_container">
					<div id="account_con_left">
						<div className="dishes-info">
							<div className="info-title">
								<span>{orderInfo.orderID}</span>
							</div>
							<ul>
								<li><i className="iconfont icon-order_icon_desk"></i>{orderInfo.tableName}</li>
								<li><i className="iconfont icon-order_icon_number"></i>{orderInfo.peopleNum}</li>
								<li><i className="iconfont icon-order_icon_waiter"></i>{orderInfo.waiterName}</li>
								<li><i className="iconfont icon-oeder_icon_time"></i>{orderInfo.createTime}</li>
								<li><i className="iconfont icon-account_icon_shichang"></i>{new Date().toLocaleTimeString()}</li>
								<li><i className="iconfont icon-account_icon_caozuoren"></i>刘德华</li>
							</ul>

							{remark}
							{relevanceTable}
						</div>

						<div className="dishes-list">
							<div className="list-title">
								<span>序号</span>
								<span>名称(规格)</span>
								<span>数量</span>
								<span>金额</span>
							</div>
							<div className="list-content">
								<Scrollbars>
									<ul>
										{settlementStore.didOrderList&&
											settlementStore.didOrderList.orderProductList&&
											settlementStore.didOrderList.orderProductList.map((dishes, index) => {
												let didOrder = [];
												didOrder.push(<DidOrder 
													key={dishes.detailID} 
													index={index} 
													settlementStore={settlementStore} 
													dishes={dishes}
													
												/>);
												if(dishes.childs && dishes.childs.length) {
													dishes.childs.map(child => {
														didOrder.push(<DidOrder 
															key={child.recordID} 
															index={-1}
															settlementStore={settlementStore} 
															dishes={child}
															
														/>)
													})
												}
												return didOrder;
											})}
									</ul>
								</Scrollbars>

							</div>
							<div className="list-all">
								<div> 总数：<span>{settlementStore.didOrderList.subTotalNumber}</span></div>
								<div>总金额：<span>{settlementStore.didOrderList.subTotalAmount}</span></div>
							</div>

						</div>
						
					</div>
					 <div id="account_con_right">
						<div className="discount-block">
							<div className="title-block">
								<span>订单金额</span>
								<span className="title-dis-number">{settlementStore.activeDisCountType}</span>
								<span className={classnames({
									"title-dis-number":true,
									"title-btn-hide":!settlementStore.showDiscount
								})}>折扣方式：{settlementStore.discountMsg}</span>
								<span className={classnames({
									"title-btn":true,
									// "title-btn-hide":!settlementStore.showDiscount
									"title-btn-hide":true,
								})} onClick={()=>{
									settlementStore.getCancleDiscount(this.props.params.subOrderID,1)
								}}>取消折扣</span>
							</div>
							<div className="content">
								<div className="message-block">
									<ul className="info">
										<li><span className="title">消费金额：</span>{settlementStore.amountSituation.totalAmount}</li>
										<li><span className="title">赠送金额：</span>{settlementStore.amountSituation.giveProductAmount}</li>
										<li><span className="title">折扣金额：</span>{settlementStore.amountSituation.discountAmount}</li>
										{freeServiceStyle}
										<li><span className="title">减免金额：</span>{settlementStore.amountSituation.reductionAmount}</li>
										<li><span className="title">减 免 人：</span>{settlementStore.amountSituation.reductionPerson}</li>
										<li><span className="title">抹零金额：</span>{settlementStore.amountSituation.droptailAmount}</li>
										<li><span className="title">兑换商品：</span>0.00</li>
									</ul>
									{payableAmount}
								</div>

								<ul className="discount">
									{btnBlock}
								</ul>

							</div>
						</div>
						<div className="pay-block">
							<div className="title-block">
								<div className="my-title">收款情况</div>
								<ul className="money">
									<li><span className="title">已收：</span>{settlementStore.amountReceived}</li>
									<li><span className="title">定金：</span>{settlementStore.amountSituation.bookingAmount}</li>
									<li className="red"><span className="title">待收：</span>{settlementStore.waitCollectAmount}</li>
									<li ><span className="title">找零：</span>{settlementStore.oddChange}</li>
									<li ><span className="title">可开票金额：</span>{settlementStore.invoiceAmount.toFixed(2)}</li>
								</ul>
							</div>
							<div className="has-gathering">
								<div className="gathering-title">已收款</div>
								<ul className="pay-items">
									{receivableBlock}
								</ul>
							</div>
							<ul className="pay-style">
								{settlementStore.paymentList.map((payment,index) => {
									return <PayMent key={index} payMent={payment} clickHandle={()=>{
										if(settlementStore.waitCollectAmount==0){//判断收款是否足够
											message.info("收款已足够，不需再收款");
										}else{
											if(settlementStore.confirmPaymentStatus){//判断在线支付是否已确定和取消
												message.info("请先确定或取消在线支付");
											}else{
												this.addPayMent(payment);
											}
										}
									}
										
									} />
								})}
							</ul>
						</div>
						<div className="footer">
							<div onClick={()=>{
								if(this.state.stopSettlement=="暂结"){
									settlementStore.getTemporarily({subOrderID:this.props.params.subOrderID});
									this.setState({stopSettlement:"取消暂结"})
								}else{
									this.setState({stopSettlement:"暂结"})
									message.info("恢复暂结成功")
								}
							}}>{this.state.stopSettlement}</div>
							
							<div onClick={()=>{
								settlementStore.getInvoicePopup();
							}}>开发票</div>
							<div className={classnames({
								'active':true,
								'disable':!settlementStore.settlementBtnDisable,
							})} onClick={()=>{
								if(settlementStore.waitCollectAmount>0){
									message.info("收款未完成");
								}else{
									settlementStore.paymentCashAll=settlementStore.receivableDate.filter((ele,index)=> {
										return ele.paymentMethodID===5
									});
									let cash=0;
									settlementStore.paymentCashAll.forEach((ele,index)=>{
											cash+=ele.paymentAmount*1;
										});
										//如果有找零金额
									if(settlementStore.oddChange>0){
										cash=cash-settlementStore.oddChange*1;
									}
									if(settlementStore.paymentCashAll.length>0){
										settlementStore.cashLish.push({isOnline:settlementStore.paymentCashAll[0].isOnline,paymentAmount:cash,paymentMethodID:settlementStore.paymentCashAll[0].paymentMethodID})							
										
										settlementStore.getOrderSettlement(this.props.params.subOrderID,settlementStore.cashLish,()=>{
											if(this.props.location.state && this.props.location.state.prevRouter === 'dishes') {
												this.context.router.push('/dine');//返回到指定页面
											}else {
												this.context.router.goBack();//返回上一个进入的页面
											}
										});
									}else{
										console.log(settlementStore.cashLish,'222')
										settlementStore.getOrderSettlement(this.props.params.subOrderID,()=>{
											if(this.props.location.state && this.props.location.state.prevRouter === 'dishes') {
												this.context.router.push('/dine');//返回到指定页面
											}else {
												this.context.router.goBack();//返回上一个进入的页面
											}
										});
										if(this.props.location.state && this.props.location.state.prevRouter === 'dishes') {
											this.context.router.push('/dine');
										}else {
											this.context.router.goBack();
										}
									}
								}
								
							}}>结账</div>
						</div>
           		 	</div>
				</div>

				{morePopup}
				{adjustProduct}
				{discountPopup}
				{invoicepopup}
				{operatePrompt}
				{categoryDiscountPopup}
				{this.state.discountPricePopup}
				{this.state.popup}
				{this.state.wechat}
				{this.state.memberpopup}
				{this.state.memberPayment}
				{this.state.memberInfo}
				{this.state.refundpopup}
				{this.state.promptpopup}
				{this.state.accreditpopup}
    		</div>
		)
	}

}
/**************** 消息中心主容器组件 *****************/

Settlement.wrappedComponent.contextTypes = {
	router: React.PropTypes.object.isRequired
}

export default Settlement;



